.app {
	display: grid;
	place-items: center;
	background-color: #dadbd3;
	height: 100vh;
}
.app__body {
	display: flex;
	background-color: #ededed;
	margin-top: -30px;
	width: 90vw;
	height: 90vh;
	box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.2);
	-webkit-animation: fadein 3s;
}
@keyframes fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
