
.sideBar {
	display: flex;
	flex-direction: column;
	flex: 0.35;
}

.sideBar__header {
	display: flex;
	justify-content: space-between;
	padding: 20px;
	border-right: 1px solid lightgray;
}

.sideBar__headerRight {
	display: flex;
	align-items: center;
	justify-content: space-between;
	min-width: 10vw;
}

.sideBar__headerRight > .MuiSvgIcon-root {
	margin-right: 2vw;
	font-size: 24px !important;
}

.sideBar__search {
	display: flex;
	align-items: center;
	background-color: #f6f6f6;
	height: 39px;
	padding: 10px;
}

.sideBar__searchContainer {
	display: flex;
	align-items: center;
	background-color: white;
	width: 100%;
	height: 35px;
	border-radius: 20px;
}
.sideBar__searchContainer > input {
	border: none;
	outline: none;
	margin-left: 10px;
}
.sideBar__searchContainer > .MuiSvgIcon-root {
	color: gray;
	padding: 10px;
}
.sideBar__chats {
	flex: 1;
	background-color: white;
	overflow-y: scroll;
}
.sideBar__chats::-webkit-scrollbar{
	display: none;
}